import { NavLink } from 'react-router-dom'
import s from './Navbar.module.css'


const Navbar = () => {
  return (
    <nav className={s.nav}>
        <div className={s.item}>
          <NavLink to='/profile' className={SelectedLink()}></NavLink>
        </div>
        <div className={s.item}>
          <NavLink to='/dialogs' className={SelectedLink()}></NavLink>
          </div>
        <div className={s.item}>
          <NavLink to='/news' className={SelectedLink()}> </NavLink>
        </div>
        <div className={s.item}>
          <NavLink to='/music' className={SelectedLink()}></NavLink>
        </div>
        <div className={s.item}>
          <NavLink to='/users' className={SelectedLink()}></NavLink>
        </div>
        <div className={s.item}>
          <NavLink to='/settings' className={SelectedLink()}></NavLink>
        </div>
      </nav>
  )
}


const SelectedLink = () => {
  return (
    select => select.isActive ? s.activeLink : s.item
  )
}



export default Navbar